'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip,
  ResponsiveContainer
} from 'recharts'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Skeleton } from '@/components/ui/skeleton'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Tooltip as TooltipUI,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import {
  TrendingUp, TrendingDown, RefreshCw, Clock, Brain,
  PieChart, BarChart3, Target, ChevronRight
} from 'lucide-react'
import { SectorAnalysisResponse } from '@/lib/types/ai-advisor'

interface SectorAnalysisProps {
  data?: SectorAnalysisResponse & {
    performance?: number
    sentiment?: string
    topPerformers?: { symbol: string; change: number }[]
    worstPerformers?: { symbol: string; change: number }[]
    history?: { date: string; value: number }[]
  }
  sector?: string
  loading?: boolean
  onRefresh?: () => void
  className?: string
}

export function SectorAnalysis({
  data,
  sector = 'Technology',
  loading = false,
  onRefresh,
  className
}: SectorAnalysisProps) {
  const [activeTab, setActiveTab] = React.useState('summary')

  const performance = data?.performance !== undefined ? data.performance * 100 : null
  const isPositive = performance !== null && performance >= 0

  const chartData = React.useMemo(() => {
    if (!data?.history) return []
    return data.history.map(d => ({
      date: new Date(d.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
      value: d.value * 100
    }))
  }, [data?.history])

  if (loading) {
    return (
      <div className={cn('space-y-4', className)}>
        <div className="flex items-center justify-between">
          <Skeleton className="h-12 w-64" />
          <Skeleton className="h-10 w-24" />
        </div>
        <Skeleton className="h-96 border-2 border-foreground" />
      </div>
    )
  }

  if (!data) {
    return (
      <Card className={cn('rounded-none border-2 border-foreground', className)}>
        <CardContent className="p-12 text-center">
          <PieChart className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
          <h3 className="font-black uppercase text-lg mb-2">No Sector Analysis</h3>
          <p className="text-muted-foreground font-mono text-xs mb-4">
            AI-powered sector analysis for {sector} will appear here
          </p>
          {onRefresh && (
            <Button
              onClick={onRefresh}
              variant="outline"
              className="rounded-none border-2 font-bold uppercase"
            >
              <RefreshCw className="h-4 w-4 mr-2" />
              Load Analysis
            </Button>
          )}
        </CardContent>
      </Card>
    )
  }

  return (
    <div className={cn('space-y-4', className)}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className={cn('h-12 w-12 flex items-center justify-center border-2 border-foreground shadow-[4px_4px_0px_0px_var(--foreground)]', {
            'bg-green-500/10': isPositive,
            'bg-red-500/10': !isPositive,
          })}>
            <PieChart className={cn('h-6 w-6', isPositive ? 'text-green-500' : 'text-red-500')} />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-lg">{data.sector}</span>
              <Badge className={cn('rounded-none font-mono text-xs', {
                'bg-green-500/10 text-green-500': isPositive,
                'bg-red-500/10 text-red-500': !isPositive,
              })}>
                {isPositive ? <TrendingUp className="h-3 w-3 mr-1" /> : <TrendingDown className="h-3 w-3 mr-1" />}
                {performance !== null ? `${performance >= 0 ? '+' : ''}${performance.toFixed(2)}%` : 'N/A'}
              </Badge>
            </div>
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <Clock className="h-3 w-3" />
              <span className="font-mono text-xs">Updated {new Date(data.last_updated).toLocaleString()}</span>
            </div>
          </div>
        </div>
        {onRefresh && (
          <Button
            onClick={onRefresh}
            variant="outline"
            size="sm"
            className="rounded-none border-2 font-bold uppercase"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            Refresh
          </Button>
        )}
      </div>

      <Card className="rounded-none border-2 border-foreground">
        <Tabs value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="h-12 rounded-none border-b-2 bg-muted/50 p-1">
            <TabsTrigger
              value="summary"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Target className="h-4 w-4 mr-2" />
              Summary
            </TabsTrigger>
            <TabsTrigger
              value="performers"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <TrendingUp className="h-4 w-4 mr-2" />
              Performers
            </TabsTrigger>
            <TabsTrigger
              value="history"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <BarChart3 className="h-4 w-4 mr-2" />
              History
            </TabsTrigger>
          </TabsList>

          <TabsContent value="summary" className="m-0 p-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Performance</div>
                <div className={cn('text-2xl font-black font-mono', isPositive ? 'text-green-500' : 'text-red-500')}>
                  {performance !== null ? `${performance >= 0 ? '+' : ''}${performance.toFixed(2)}%` : 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Sentiment</div>
                <div className="text-2xl font-black font-mono capitalize">
                  {data.sentiment || 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">AI Analysis</div>
                <div className="text-sm font-medium truncate">
                  {data.summary?.substring(0, 50) || 'N/A'}...
                </div>
              </div>
            </div>

            <div className="p-4 bg-muted/20 border-2 border-foreground/10">
              <p className="text-sm leading-relaxed">{data.summary}</p>
            </div>
          </TabsContent>

          <TabsContent value="performers" className="m-0 p-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h4 className="font-black uppercase text-sm mb-4 flex items-center gap-2">
                  <TrendingUp className="h-4 w-4 text-green-500" />
                  Top Performers
                </h4>
                <div className="space-y-2">
                  {data.topPerformers?.map((stock, i) => (
                    <div key={stock.symbol} className="flex items-center justify-between p-3 bg-green-500/5 border border-green-500/20">
                      <div className="flex items-center gap-2">
                        <span className="font-bold">{stock.symbol}</span>
                        <span className="text-xs text-muted-foreground">#{i + 1}</span>
                      </div>
                      <span className="text-green-500 font-mono font-bold">
                        +{stock.change.toFixed(2)}%
                      </span>
                    </div>
                  )) || (
                    <p className="text-sm text-muted-foreground">No data available</p>
                  )}
                </div>
              </div>

              <div>
                <h4 className="font-black uppercase text-sm mb-4 flex items-center gap-2">
                  <TrendingDown className="h-4 w-4 text-red-500" />
                  Worst Performers
                </h4>
                <div className="space-y-2">
                  {data.worstPerformers?.map((stock, i) => (
                    <div key={stock.symbol} className="flex items-center justify-between p-3 bg-red-500/5 border border-red-500/20">
                      <div className="flex items-center gap-2">
                        <span className="font-bold">{stock.symbol}</span>
                        <span className="text-xs text-muted-foreground">#{i + 1}</span>
                      </div>
                      <span className="text-red-500 font-mono font-bold">
                        {stock.change.toFixed(2)}%
                      </span>
                    </div>
                  )) || (
                    <p className="text-sm text-muted-foreground">No data available</p>
                  )}
                </div>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="history" className="m-0 p-6">
            {chartData.length > 0 ? (
              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
                    <XAxis dataKey="date" tick={{ fontSize: 11, fontFamily: 'monospace' }} />
                    <YAxis tick={{ fontSize: 11, fontFamily: 'monospace' }} tickFormatter={(v) => `${v.toFixed(0)}%`} />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'var(--background)',
                        border: '2px solid var(--foreground)',
                        borderRadius: 0,
                        boxShadow: '4px 4px 0px 0px var(--foreground)',
                      }}
                      formatter={(value: number) => [`${value.toFixed(2)}%`, 'Performance']}
                    />
                    <Line
                      type="monotone"
                      dataKey="value"
                      stroke="var(--primary)"
                      strokeWidth={2}
                      dot={{ r: 3, fill: 'var(--primary)' }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            ) : (
              <div className="p-12 text-center text-muted-foreground">
                <BarChart3 className="h-8 w-8 mx-auto mb-3 opacity-50" />
                <p className="text-sm font-mono uppercase">No historical data available</p>
              </div>
            )}
          </TabsContent>
        </Tabs>
      </Card>
    </div>
  )
}
