function getSentimentColor(score: number) {
  if (score >= 0.6) return { bg: 'bg-green-100', text: 'text-green-700', border: 'border-green-500', light: 'bg-green-50', chart: '#22c55e' }
  if (score >= 0.4) return { bg: 'bg-yellow-100', text: 'text-yellow-700', border: 'border-yellow-500', light: 'bg-yellow-50', chart: '#eab308' }
  if (score >= 0.2) return { bg: 'bg-orange-100', text: 'text-orange-700', border: 'border-orange-500', light: 'bg-orange-50', chart: '#f97316' }
  return { bg: 'bg-red-100', text: 'text-red-700', border: 'border-red-500', light: 'bg-red-50', chart: '#ef4444' }
}

function getTrendIcon(trend: string) {
  if (trend === 'improving') return TrendingUp
  if (trend === 'declining') return TrendingDown
  return Minus
}

function getSignalIcon(type: string) {
  if (type === 'bullish') return TrendingUp
  if (type === 'bearish') return TrendingDown
  return Minus
}

function getCategoryIcon(category: string) {
  switch (category.toLowerCase()) {
    case 'earnings': return TrendingUp
    case 'technical': return LineChartIcon
    case 'options flow': return BarChart2
    case 'analyst': return Target
    case 'social media': return Twitter
    case 'insider trading': return Eye
    default: return Globe
  }
}

function SentimentGauge({ score, label, size = 'md', showProgress = false }: { score: number; label: string; size?: 'sm' | 'md' | 'lg'; showProgress?: boolean }) {
  const colors = getSentimentColor(score)
  const radius = size === 'lg' ? 60 : size === 'md' ? 40 : 28
  const stroke = size === 'lg' ? 12 : size === 'md' ? 8 : 5
  const circumference = 2 * Math.PI * radius
  const offset = circumference - score * circumference

  return (
    <div className="flex flex-col items-center">
      <div className="relative" style={{ width: radius * 2 + stroke, height: radius * 2 + stroke }}>
        <svg className="transform -rotate-90" width={radius * 2 + stroke} height={radius * 2 + stroke}>
          <circle
            cx={radius + stroke / 2}
            cy={radius + stroke / 2}
            r={radius}
            fill="none"
            stroke="currentColor"
            strokeWidth={stroke}
            className="text-muted"
            strokeDasharray={circumference}
            strokeDashoffset={0}
          />
          <circle
            cx={radius + stroke / 2}
            cy={radius + stroke / 2}
            r={radius}
            fill="none"
            stroke="currentColor"
            strokeWidth={stroke}
            className={cn('transition-all duration-500', colors.text.replace('text-', 'stroke-'))}
            strokeDasharray={circumference}
            strokeDashoffset={offset}
            strokeLinecap="round"
          />
        </svg>
        <div className="absolute inset-0 flex flex-col items-center justify-center">
          <span className={cn('font-black', size === 'lg' ? 'text-3xl' : size === 'md' ? 'text-xl' : 'text-sm')}>
            {Math.round(score * 100)}
          </span>
          {showProgress && (
            <Progress value={score * 100} className="w-12 h-1 mt-1" />
          )}
        </div>
      </div>
      <span className="text-xs font-bold uppercase mt-1 text-muted-foreground">{label}</span>
    </div>
  )
}

