function InsightCard({ insight }: { insight: AIAnalysisInsight }) {
  const colors = getSentimentColor(insight.sentiment_score || 0.5)
  const impactColors = {
    high: { bg: 'bg-red-50', border: 'border-l-red-500', badge: 'bg-red-100 text-red-700' },
    medium: { bg: 'bg-yellow-50', border: 'border-l-yellow-500', badge: 'bg-yellow-100 text-yellow-700' },
    low: { bg: 'bg-green-50', border: 'border-l-green-500', badge: 'bg-green-100 text-green-700' },
  }
  const impact = impactColors[insight.impact]
  const Icon = getCategoryIcon(insight.category)
  const shiftSymbol = insight.sentiment_shift > 0 ? '+' : ''
  const shiftColor = insight.sentiment_shift > 0 ? 'text-green-600' : 'text-red-600'

  return (
    <Card className={cn('transition-all hover:shadow-md', impact.border)}>
      <CardContent className="p-4">
        <div className="flex items-start justify-between mb-3">
          <div className="flex items-center gap-2">
            <div className={cn('p-2 rounded-lg', colors.light)}>
              <Icon className={cn('h-4 w-4', colors.text)} />
            </div>
            <div>
              <Badge className={cn('text-xs font-bold uppercase', impact.badge)}>
                {insight.impact} Impact
              </Badge>
              <span className="text-xs text-muted-foreground ml-2">{insight.category}</span>
            </div>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-xs text-muted-foreground">{insight.source}</span>
            <span className={cn('text-xs font-bold', shiftColor)}>
              {shiftSymbol}{formatPercent(insight.sentiment_shift)}
            </span>
          </div>
        </div>

        <h4 className="font-semibold text-sm mb-1">{insight.news_headline}</h4>
        <p className="text-sm text-muted-foreground mb-3">{insight.insight}</p>

        {insight.related_symbols && insight.related_symbols.length > 0 && (
          <div className="flex items-center gap-2 mb-3">
            <span className="text-xs text-muted-foreground">Related:</span>
            <div className="flex gap-1">
              {insight.related_symbols.map((sym) => (
                <Badge key={sym} variant="outline" className="text-xs">${sym}</Badge>
              ))}
            </div>
          </div>
        )}

        <div className="flex items-center justify-between pt-3 border-t">
          <span className="text-xs text-muted-foreground">
            {new Date(insight.timestamp).toLocaleDateString('en-US', { 
              month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit' 
            })}
          </span>
          {insight.sentiment_score !== undefined && (
            <div className="flex items-center gap-1">
              <span className="text-xs text-muted-foreground">Sentiment:</span>
              <span className={cn('text-xs font-bold', colors.text)}>
                {Math.round(insight.sentiment_score * 100)}%
              </span>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  )
}

