'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip,
  ResponsiveContainer, Area, ComposedChart
} from 'recharts'
import { ForecastNarrativeResponse } from '@/lib/types/ai-advisor'
import { TrendingUp, TrendingDown, Activity, Zap } from 'lucide-react'
import { Badge } from '@/components/ui/badge'

interface ForecastChartProps {
  data: ForecastNarrativeResponse
  className?: string
  showHistorical?: boolean
}

export function ForecastChart({ data, className, showHistorical = true }: ForecastChartProps) {
  const chartData = React.useMemo(() => {
    const points: Array<{
      date: string
      price: number | null
      predicted: number | null
      upper: number | null
      lower: number | null
      type: 'historical' | 'forecast'
    }> = []

    data.forecast.forEach((point) => {
      const isHistorical = point.date < new Date().toISOString().split('T')[0]
      points.push({
        date: new Date(point.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
        price: isHistorical ? point.predicted_price : null,
        predicted: !isHistorical ? point.predicted_price : null,
        upper: !isHistorical ? point.confidence_upper : null,
        lower: !isHistorical ? point.confidence_lower : null,
        type: isHistorical ? 'historical' : 'forecast',
      })
    })

    return points
  }, [data])

  const lastPoint = chartData[chartData.length - 1]
  const firstPoint = chartData[0]
  const change = lastPoint?.predicted && firstPoint?.price
    ? ((lastPoint.predicted - firstPoint.price) / firstPoint.price) * 100
    : 0

  const minPrice = Math.min(...chartData.filter(d => d.lower !== null).map(d => d.lower!))
  const maxPrice = Math.max(...chartData.filter(d => d.upper !== null).map(d => d.upper!))

  return (
    <div className={cn('brutalist-glass border-2 border-foreground bg-background', className)}>
      <div className="p-4 border-b-2 border-foreground flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="h-10 w-10 bg-primary/10 flex items-center justify-center border-2 border-primary/30">
            <Activity className="h-5 w-5 text-primary" />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-sm">{data.symbol}</span>
              <Badge className={cn(
                'rounded-none font-mono text-[9px]',
                data.sentiment_label === 'bullish' ? 'bg-green-500' :
                data.sentiment_label === 'bearish' ? 'bg-red-500' : 'bg-muted'
              )}>
                {data.sentiment_label.toUpperCase()}
              </Badge>
            </div>
            <div className="flex items-center gap-2">
              <span className="text-xs font-mono text-muted-foreground">
                Current: ${data.current_price.toLocaleString()}
              </span>
              <span className={cn(
                'flex items-center gap-1 text-xs font-bold',
                change >= 0 ? 'text-green-500' : 'text-red-500'
              )}>
                {change >= 0 ? <TrendingUp className="h-3 w-3" /> : <TrendingDown className="h-3 w-3" />}
                {change >= 0 ? '+' : ''}{change.toFixed(2)}%
              </span>
            </div>
          </div>
        </div>
        <div className="flex items-center gap-2">
          <div className="text-right">
            <span className="text-[9px] font-mono uppercase text-muted-foreground block">Forecast</span>
            <span className="font-black font-mono">
              ${lastPoint?.predicted?.toFixed(2) || 'N/A'}
            </span>
          </div>
        </div>
      </div>

      <div className="p-4">
        <div className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <ComposedChart data={chartData} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
              <defs>
                <linearGradient id="confidenceGradient" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="var(--primary)" stopOpacity={0.2} />
                  <stop offset="95%" stopColor="var(--primary)" stopOpacity={0} />
                </linearGradient>
              </defs>
              <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
              <XAxis
                dataKey="date"
                tick={{ fontSize: 10, fontFamily: 'monospace' }}
                tickLine={false}
                axisLine={{ stroke: 'var(--border)' }}
              />
              <YAxis
                domain={[minPrice * 0.95, maxPrice * 1.05]}
                tick={{ fontSize: 10, fontFamily: 'monospace' }}
                tickLine={false}
                axisLine={false}
                tickFormatter={(value) => `$${(value / 1000).toFixed(1)}k`}
              />
              <Tooltip
                contentStyle={{
                  backgroundColor: 'var(--background)',
                  border: '2px solid var(--foreground)',
                  borderRadius: 0,
                  boxShadow: '4px 4px 0px 0px var(--foreground)',
                }}
                labelStyle={{ fontFamily: 'monospace', fontSize: 11, marginBottom: 4 }}
                itemStyle={{ fontFamily: 'monospace', fontSize: 11 }}
                formatter={(value: number, name: string) => [
                  `$${value.toFixed(2)}`,
                  name === 'price' ? 'Historical' : name === 'predicted' ? 'Predicted' : name
                ]}
              />
              <Area
                type="monotone"
                dataKey="upper"
                stroke="none"
                fill="url(#confidenceGradient)"
                fillOpacity={1}
              />
              <Area
                type="monotone"
                dataKey="lower"
                stroke="none"
                fill="var(--background)"
                fillOpacity={1}
              />
              <Line
                type="monotone"
                dataKey="price"
                stroke="var(--muted-foreground)"
                strokeWidth={2}
                dot={false}
                strokeDasharray="5 5"
              />
              <Line
                type="monotone"
                dataKey="predicted"
                stroke="var(--primary)"
                strokeWidth={3}
                dot={{ r: 4, fill: 'var(--primary)', strokeWidth: 0 }}
                activeDot={{ r: 6, fill: 'var(--primary)' }}
              />
            </ComposedChart>
          </ResponsiveContainer>
        </div>

        <div className="mt-4 p-3 bg-muted/20 border-2 border-foreground/10">
          <div className="flex items-center gap-2 mb-2">
            <Zap className="h-3 w-3 text-primary" />
            <span className="text-[10px] font-black uppercase">AI Analysis</span>
          </div>
          <p className="text-xs leading-relaxed">{data.narrative}</p>
          <div className="flex flex-wrap gap-2 mt-3">
            {data.key_factors.map((factor, i) => (
              <Badge key={i} variant="outline" className="rounded-none text-[9px] font-mono">
                {factor}
              </Badge>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
