'use client'

import { useState } from 'react'
import { Template, TEMPLATE_TYPE_LABELS } from '@/lib/types/ai-templates'
import { TierBadge, StaleBadge } from './TierBadge'
import { DeltaUpdateCard } from './DeltaUpdate'
import { cn } from '@/lib/utils'
import {
  Clock,
  RefreshCw,
  ChevronRight,
  FileText,
  TrendingUp,
  TrendingDown,
  Minus
} from 'lucide-react'

interface TemplateCardProps {
  template: Template
  onSelect?: (template: Template) => void
  onRefresh?: (template: Template) => void
  showDelta?: boolean
  delta?: { direction: 'up' | 'down' | 'neutral'; change: number; message: string } | null
}

export function TemplateCard({
  template,
  onSelect,
  onRefresh,
  showDelta = false,
  delta
}: TemplateCardProps) {
  const [isRefreshing, setIsRefreshing] = useState(false)
  const [isHovered, setIsHovered] = useState(false)

  const handleRefresh = async () => {
    if (isRefreshing) return
    setIsRefreshing(true)
    try {
      await onRefresh?.(template)
    } finally {
      setTimeout(() => setIsRefreshing(false), 1000)
    }
  }

  const handleRefreshWithEvent = async (e: React.MouseEvent) => {
    e.stopPropagation()
    handleRefresh()
  }

  const getDeltaIcon = () => {
    if (!delta) return null
    switch (delta.direction) {
      case 'up':
        return <TrendingUp className="w-4 h-4 text-green-500" />
      case 'down':
        return <TrendingDown className="w-4 h-4 text-red-500" />
      default:
        return <Minus className="w-4 h-4 text-gray-500" />
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60))
    const diffMins = Math.floor(diffMs / (1000 * 60))

    if (diffMins < 60) {
      return `${diffMins}m ago`
    } else if (diffHours < 24) {
      return `${diffHours}h ago`
    } else {
      return date.toLocaleDateString()
    }
  }

  return (
    <div
      className={cn(
        'group relative overflow-hidden rounded-none border bg-card transition-all duration-200',
        'hover:border-primary/50 hover:shadow-lg',
        isHovered && 'ring-2 ring-primary/20',
        template.is_stale && 'border-yellow-500/50'
      )}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={() => onSelect?.(template)}
    >
      {/* Stale indicator bar */}
      {template.is_stale && (
        <div className="absolute top-0 left-0 right-0 h-1 bg-yellow-500 animate-pulse" />
      )}

      <div className="p-4">
        {/* Header */}
        <div className="flex items-start justify-between gap-3">
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2 mb-1">
              <FileText className="w-4 h-4 text-muted-foreground shrink-0" />
              <span className="text-xs text-muted-foreground">
                {TEMPLATE_TYPE_LABELS[template.template_type]}
              </span>
            </div>
            <h3 className="font-bold text-lg leading-tight truncate">{template.title}</h3>
          </div>
          <TierBadge tier={template.access_tier} showLabel={false} size="sm" />
        </div>

        {/* Summary */}
        <p className="text-sm text-muted-foreground mt-3 line-clamp-2">
          {template.summary}
        </p>

        {/* Delta update */}
        {showDelta && delta && (
          <div
            className={cn(
              'mt-3 p-2 rounded border',
              delta.direction === 'up' && 'bg-green-500/10 border-green-500/30',
              delta.direction === 'down' && 'bg-red-500/10 border-red-500/30',
              delta.direction === 'neutral' && 'bg-gray-500/10 border-gray-500/30'
            )}
          >
            <div className="flex items-center gap-2">
              {getDeltaIcon()}
              <span className={cn(
                'font-mono font-bold text-sm',
                delta.direction === 'up' && 'text-green-500',
                delta.direction === 'down' && 'text-red-500',
                delta.direction === 'neutral' && 'text-gray-500'
              )}>
                {delta.direction === 'up' ? '+' : delta.direction === 'down' ? '' : ''}{delta.change}%
              </span>
              <span className="text-xs text-muted-foreground">since analysis</span>
            </div>
            <p className="text-xs mt-1 text-muted-foreground">{delta.message}</p>
          </div>
        )}

        {/* Footer */}
        <div className="flex items-center justify-between mt-4 pt-3 border-t border-border/50">
          <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
            <Clock className="w-3 h-3" />
            <span>{formatDate(template.last_generated_at)}</span>
          </div>

          <div className="flex items-center gap-2">
            {template.is_stale && (
              <StaleBadge onRefresh={handleRefresh} />
            )}
            <button
              onClick={handleRefresh}
              disabled={isRefreshing}
              className={cn(
                'p-1.5 rounded text-muted-foreground hover:text-foreground transition-colors',
                'disabled:opacity-50',
                isRefreshing && 'animate-spin'
              )}
              title="Refresh template"
            >
              <RefreshCw className="w-4 h-4" />
            </button>
            <ChevronRight className="w-4 h-4 text-muted-foreground group-hover:text-foreground transition-colors" />
          </div>
        </div>
      </div>

      {/* Hover effect gradient */}
      <div className="absolute inset-0 bg-gradient-to-r from-primary/5 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none" />
    </div>
  )
}
