'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import { UsageStatsResponse, checkAccessStatus } from '@/lib/api/ai-advisor'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { Zap, RefreshCw, AlertTriangle } from 'lucide-react'

interface UsageMeterProps {
  className?: string
  compact?: boolean
}

export function UsageMeter({ className, compact = false }: UsageMeterProps) {
  const [stats, setStats] = React.useState<UsageStatsResponse | null>(null)
  const [access, setAccess] = React.useState<{ has_access: boolean; reason?: string } | null>(null)
  const [loading, setLoading] = React.useState(true)
  const [error, setError] = React.useState<string | null>(null)

  const fetchData = React.useCallback(async () => {
    try {
      const [statsData, accessData] = await Promise.all([
        getUsageStats().catch(() => null),
        checkAccessStatus('ai_advisor').catch(() => null),
      ])
      setStats(statsData)
      setAccess(accessData)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch usage')
    } finally {
      setLoading(false)
    }
  }, [])

  React.useEffect(() => {
    fetchData()
  }, [fetchData])

  if (loading) {
    return (
      <div className={cn('animate-pulse', className)}>
        <div className="h-16 bg-muted rounded-none border-2 border-foreground/20" />
      </div>
    )
  }

  if (error || !stats) {
    return (
      <div className={cn('brutalist-glass p-4 border-2 border-red-500/30', className)}>
        <div className="flex items-center gap-2 text-red-500">
          <AlertTriangle className="h-4 w-4" />
          <span className="text-xs font-mono uppercase">{error || 'Failed to load'}</span>
        </div>
      </div>
    )
  }

  const percentage = (stats.requests_this_month / stats.requests_limit) * 100
  const isNearLimit = percentage >= 80
  const isAtLimit = percentage >= 100

  if (compact) {
    return (
      <div className={cn('flex items-center gap-3', className)}>
        <Zap className={cn('h-4 w-4', isAtLimit ? 'text-red-500' : 'text-primary')} />
        <div className="flex-1">
          <div className="flex justify-between text-xs mb-1">
            <span className="font-mono uppercase">{stats.requests_this_month}/{stats.requests_limit}</span>
            <span className={cn(
              'font-mono',
              isAtLimit ? 'text-red-500' : isNearLimit ? 'text-yellow-500' : 'text-muted-foreground'
            )}>
              {Math.round(percentage)}%
            </span>
          </div>
          <Progress
            value={Math.min(percentage, 100)}
            className="h-1 rounded-none"
            style={{
              '--progress-fill': isAtLimit ? '#ef4444' : isNearLimit ? '#eab308' : 'hsl(var(--primary))'
            } as React.CSSProperties}
          />
        </div>
        <Badge className={cn(
          'rounded-none font-mono text-[9px]',
          isAtLimit ? 'bg-red-500' : isNearLimit ? 'bg-yellow-500' : 'bg-primary'
        )}>
          {isAtLimit ? 'LIMIT' : stats.plan_type.toUpperCase()}
        </Badge>
      </div>
    )
  }

  return (
    <div className={cn('brutalist-glass border-2 border-foreground bg-background', className)}>
      <div className="p-4 border-b-2 border-foreground flex items-center justify-between">
        <div className="flex items-center gap-2">
          <Zap className="h-4 w-4 text-primary" />
          <span className="font-black uppercase text-xs tracking-widest">AI Usage</span>
        </div>
        <button
          onClick={fetchData}
          className="p-1 hover:bg-muted transition-colors"
          title="Refresh"
        >
          <RefreshCw className="h-3 w-3" />
        </button>
      </div>

      <div className="p-4 space-y-4">
        <div>
          <div className="flex justify-between items-end mb-2">
            <span className="text-xs font-mono uppercase text-muted-foreground">Monthly Requests</span>
            <span className={cn(
              'text-xl font-black font-mono',
              isAtLimit ? 'text-red-500' : isNearLimit ? 'text-yellow-500' : ''
            )}>
              {stats.requests_this_month}
              <span className="text-xs text-muted-foreground font-normal">/{stats.requests_limit}</span>
            </span>
          </div>
          <Progress
            value={Math.min(percentage, 100)}
            className="h-3 rounded-none"
            style={{
              '--progress-fill': isAtLimit ? '#ef4444' : isNearLimit ? '#eab308' : 'hsl(var(--primary))'
            } as React.CSSProperties}
          />
          {isNearLimit && !isAtLimit && (
            <p className="text-[9px] font-mono text-yellow-500 mt-1">
              {Math.round(stats.requests_limit - stats.requests_this_month)} requests remaining
            </p>
          )}
        </div>

        <div className="grid grid-cols-2 gap-3">
          <div className="border-2 border-foreground/20 p-3">
            <span className="text-[9px] font-mono uppercase text-muted-foreground">Plan</span>
            <p className="font-black uppercase text-sm">{stats.plan_type}</p>
          </div>
          <div className="border-2 border-foreground/20 p-3">
            <span className="text-[9px] font-mono uppercase text-muted-foreground">Resets</span>
            <p className="font-mono text-sm">{new Date(stats.reset_date).toLocaleDateString()}</p>
          </div>
        </div>

        {isAtLimit && (
          <div className="border-2 border-red-500 p-3 bg-red-500/5">
            <div className="flex items-center gap-2 text-red-500 mb-1">
              <AlertTriangle className="h-4 w-4" />
              <span className="font-black uppercase text-xs">Limit Reached</span>
            </div>
            <p className="text-xs text-muted-foreground">
              Upgrade your plan or wait until {new Date(stats.reset_date).toLocaleDateString()} for reset.
            </p>
          </div>
        )}

        {!access?.has_access && !isAtLimit && (
          <div className="border-2 border-yellow-500/50 p-3 bg-yellow-500/5">
            <span className="font-black uppercase text-xs text-yellow-500">Premium Feature</span>
            <p className="text-xs text-muted-foreground mt-1">
              {access?.reason || 'AI Advisor requires a premium subscription.'}
            </p>
          </div>
        )}
      </div>
    </div>
  )
}

// Re-export the API functions for use in the component
async function getUsageStats(): Promise<UsageStatsResponse> {
  const response = await fetch('/ai/usage')
  if (!response.ok) {
    throw new Error('Failed to fetch usage stats')
  }
  return response.json()
}
