'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Skeleton } from '@/components/ui/skeleton'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Tooltip as TooltipUI,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import {
  RefreshCw, Clock, Info, Brain,
  Percent, Banknote, BarChart3, Target
} from 'lucide-react'
import { BondMarketAnalysisResponse } from '@/lib/types/ai-advisor'

interface BondMarketAnalysisProps {
  data?: BondMarketAnalysisResponse
  loading?: boolean
  onRefresh?: () => void
  className?: string
}

export function BondMarketAnalysis({
  data,
  loading = false,
  onRefresh,
  className
}: BondMarketAnalysisProps) {
  const [activeTab, setActiveTab] = React.useState('overview')

  if (loading) {
    return (
      <div className={cn('space-y-4', className)}>
        <div className="flex items-center justify-between">
          <Skeleton className="h-12 w-64" />
          <Skeleton className="h-10 w-24" />
        </div>
        <Skeleton className="h-96 border-2 border-foreground" />
      </div>
    )
  }

  if (!data) {
    return (
      <Card className={cn('rounded-none border-2 border-foreground', className)}>
        <CardContent className="p-12 text-center">
          <Banknote className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
          <h3 className="font-black uppercase text-lg mb-2">No Bond Analysis</h3>
          <p className="text-muted-foreground font-mono text-xs mb-4">
            AI-powered bond market analysis will appear here
          </p>
          {onRefresh && (
            <Button
              onClick={onRefresh}
              variant="outline"
              className="rounded-none border-2 font-bold uppercase"
            >
              <RefreshCw className="h-4 w-4 mr-2" />
              Load Analysis
            </Button>
          )}
        </CardContent>
      </Card>
    )
  }

  const yields = data.yield_data

  return (
    <div className={cn('space-y-4', className)}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="h-12 w-12 bg-blue-500/10 flex items-center justify-center border-2 border-blue-500/30">
            <Banknote className="h-6 w-6 text-blue-500" />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-lg">{data.title}</span>
              <Badge className="rounded-none bg-blue-500/10 text-blue-500 font-mono text-xs">
                <BarChart3 className="h-3 w-3 mr-1" />
                Bond Market
              </Badge>
            </div>
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <Clock className="h-3 w-3" />
              <span className="font-mono text-xs">Updated {new Date(data.last_updated).toLocaleString()}</span>
            </div>
          </div>
        </div>
        {onRefresh && (
          <Button
            onClick={onRefresh}
            variant="outline"
            size="sm"
            className="rounded-none border-2 font-bold uppercase"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            Refresh
          </Button>
        )}
      </div>

      <Card className="rounded-none border-2 border-foreground">
        <Tabs value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="h-12 rounded-none border-b-2 bg-muted/50 p-1">
            <TabsTrigger
              value="overview"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Target className="h-4 w-4 mr-2" />
              Overview
            </TabsTrigger>
            <TabsTrigger
              value="yields"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Percent className="h-4 w-4 mr-2" />
              Yields
            </TabsTrigger>
            <TabsTrigger
              value="analysis"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Brain className="h-4 w-4 mr-2" />
              Analysis
            </TabsTrigger>
          </TabsList>

          <TabsContent value="overview" className="m-0 p-6">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">10Y Treasury</div>
                <div className="text-2xl font-black font-mono">
                  {yields?.treasury_10y ? `${yields.treasury_10y.toFixed(2)}%` : 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">2Y Treasury</div>
                <div className="text-2xl font-black font-mono">
                  {yields?.treasury_2y ? `${yields.treasury_2y.toFixed(2)}%` : 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Corp IG</div>
                <div className="text-2xl font-black font-mono">
                  {yields?.corporate_ig ? `${yields.corporate_ig.toFixed(2)}%` : 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Corp HY</div>
                <div className="text-2xl font-black font-mono">
                  {yields?.corporate_hy ? `${yields.corporate_hy.toFixed(2)}%` : 'N/A'}
                </div>
              </div>
            </div>

            <div className="p-4 bg-muted/20 border-2 border-foreground/10">
              <p className="text-sm leading-relaxed">{data.summary}</p>
            </div>
          </TabsContent>

          <TabsContent value="yields" className="m-0 p-6">
            <div className="space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div className="p-4 bg-blue-500/5 border-2 border-blue-500/20">
                  <div className="text-xs font-mono uppercase text-muted-foreground mb-2">Treasury Yields</div>
                  <div className="space-y-2">
                    <div className="flex justify-between">
                      <span className="text-sm">10 Year</span>
                      <span className="font-mono font-bold">{yields?.treasury_10y ? `${yields.treasury_10y.toFixed(2)}%` : 'N/A'}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm">2 Year</span>
                      <span className="font-mono font-bold">{yields?.treasury_2y ? `${yields.treasury_2y.toFixed(2)}%` : 'N/A'}</span>
                    </div>
                  </div>
                </div>
                <div className="p-4 bg-purple-500/5 border-2 border-purple-500/20">
                  <div className="text-xs font-mono uppercase text-muted-foreground mb-2">Corporate Yields</div>
                  <div className="space-y-2">
                    <div className="flex justify-between">
                      <span className="text-sm">Investment Grade</span>
                      <span className="font-mono font-bold">{yields?.corporate_ig ? `${yields.corporate_ig.toFixed(2)}%` : 'N/A'}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm">High Yield</span>
                      <span className="font-mono font-bold">{yields?.corporate_hy ? `${yields.corporate_hy.toFixed(2)}%` : 'N/A'}</span>
                    </div>
                  </div>
                </div>
              </div>

              {yields && (
                <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                  <div className="text-xs font-mono uppercase text-muted-foreground mb-3">Yield Spread Analysis</div>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span>IG vs 10Y Treasury</span>
                      <span className="font-mono font-bold">
                        {yields.corporate_ig && yields.treasury_10y
                          ? `${(yields.corporate_ig - yields.treasury_10y).toFixed(2)}%`
                          : 'N/A'}
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span>HY vs 10Y Treasury</span>
                      <span className="font-mono font-bold">
                        {yields.corporate_hy && yields.treasury_10y
                          ? `${(yields.corporate_hy - yields.treasury_10y).toFixed(2)}%`
                          : 'N/A'}
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span>HY vs IG</span>
                      <span className="font-mono font-bold">
                        {yields.corporate_hy && yields.corporate_ig
                          ? `${(yields.corporate_hy - yields.corporate_ig).toFixed(2)}%`
                          : 'N/A'}
                      </span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </TabsContent>

          <TabsContent value="analysis" className="m-0 p-6">
            <div className="prose prose-sm max-w-none">
              <div className="whitespace-pre-wrap text-sm leading-relaxed">
                {data.content}
              </div>
            </div>

            <div className="mt-6 pt-6 border-t-2 border-foreground/10">
              <div className="flex items-center justify-between text-xs text-muted-foreground">
                <div className="flex items-center gap-4">
                  <span className="flex items-center gap-1">
                    <Brain className="h-4 w-4" />
                    Generated by GLM-4.7
                  </span>
                  <span className="flex items-center gap-1">
                    <Clock className="h-4 w-4" />
                    {new Date(data.last_updated).toLocaleString()}
                  </span>
                </div>
                <TooltipProvider>
                  <TooltipUI>
                    <TooltipTrigger asChild>
                      <Button variant="ghost" size="icon" className="h-8 w-8">
                        <Info className="h-4 w-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent>
                      <p className="max-w-xs text-xs">
                        Bond market analysis is generated by AI and should not be taken as investment advice.
                      </p>
                    </TooltipContent>
                  </TooltipUI>
                </TooltipProvider>
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </Card>
    </div>
  )
}
