'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Skeleton } from '@/components/ui/skeleton'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import {
  Shield, AlertTriangle, TrendingUp, TrendingDown, RefreshCw,
  Clock, Info, AlertCircle, CheckCircle, Brain, FileText
} from 'lucide-react'
import { RiskCommentaryResponse } from '@/lib/types/ai-advisor'

interface RiskCommentaryProps {
  data?: RiskCommentaryResponse
  loading?: boolean
  onRefresh?: () => void
  className?: string
}

export function RiskCommentary({
  data,
  loading = false,
  onRefresh,
  className
}: RiskCommentaryProps) {
  const [activeTab, setActiveTab] = React.useState('summary')

  if (loading) {
    return (
      <div className={cn('space-y-4', className)}>
        <div className="flex items-center justify-between">
          <Skeleton className="h-12 w-64" />
          <Skeleton className="h-10 w-24" />
        </div>
        <Skeleton className="h-96 border-2 border-foreground" />
      </div>
    )
  }

  if (!data) {
    return (
      <Card className={cn('rounded-none border-2 border-foreground', className)}>
        <CardContent className="p-12 text-center">
          <Shield className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
          <h3 className="font-black uppercase text-lg mb-2">No Risk Commentary</h3>
          <p className="text-muted-foreground font-mono text-xs mb-4">
            AI-generated market risk analysis will appear here
          </p>
          {onRefresh && (
            <Button
              onClick={onRefresh}
              variant="outline"
              className="rounded-none border-2 font-bold uppercase"
            >
              <RefreshCw className="h-4 w-4 mr-2" />
              Load Analysis
            </Button>
          )}
        </CardContent>
      </Card>
    )
  }

  return (
    <div className={cn('space-y-4', className)}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="h-12 w-12 bg-red-500/10 flex items-center justify-center border-2 border-red-500/30">
            <Shield className="h-6 w-6 text-red-500" />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-lg">{data.title}</span>
              <Badge className="rounded-none bg-red-500/10 text-red-500 font-mono text-xs">
                <AlertTriangle className="h-3 w-3 mr-1" />
                Risk Alert
              </Badge>
            </div>
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <Clock className="h-3 w-3" />
              <span className="font-mono text-xs">Updated {new Date(data.last_updated).toLocaleString()}</span>
            </div>
          </div>
        </div>
        {onRefresh && (
          <Button
            onClick={onRefresh}
            variant="outline"
            size="sm"
            className="rounded-none border-2 font-bold uppercase"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            Refresh
          </Button>
        )}
      </div>

      <Card className="rounded-none border-2 border-foreground">
        <Tabs value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="h-12 rounded-none border-b-2 bg-muted/50 p-1">
            <TabsTrigger
              value="summary"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <FileText className="h-4 w-4 mr-2" />
              Summary
            </TabsTrigger>
            <TabsTrigger
              value="analysis"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Brain className="h-4 w-4 mr-2" />
              Full Analysis
            </TabsTrigger>
          </TabsList>

          <TabsContent value="summary" className="m-0 p-6">
            <div className="space-y-6">
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <p className="text-sm leading-relaxed">{data.summary}</p>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="p-4 bg-red-500/5 border-2 border-red-500/20">
                  <div className="flex items-center gap-2 mb-2">
                    <AlertCircle className="h-5 w-5 text-red-500" />
                    <span className="font-bold uppercase text-xs">High Risk</span>
                  </div>
                  <p className="text-xs text-muted-foreground">
                    Multiple risk factors detected in current market conditions
                  </p>
                </div>

                <div className="p-4 bg-yellow-500/5 border-2 border-yellow-500/20">
                  <div className="flex items-center gap-2 mb-2">
                    <TrendingDown className="h-5 w-5 text-yellow-500" />
                    <span className="font-bold uppercase text-xs">Downside Risk</span>
                  </div>
                  <p className="text-xs text-muted-foreground">
                    Potential for significant downside in volatile sectors
                  </p>
                </div>

                <div className="p-4 bg-green-500/5 border-2 border-green-500/20">
                  <div className="flex items-center gap-2 mb-2">
                    <CheckCircle className="h-5 w-5 text-green-500" />
                    <span className="font-bold uppercase text-xs">Defensive Plays</span>
                  </div>
                  <p className="text-xs text-muted-foreground">
                    Consider defensive allocation and hedging strategies
                  </p>
                </div>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="analysis" className="m-0 p-6">
            <div className="prose prose-sm max-w-none">
              <div className="whitespace-pre-wrap text-sm leading-relaxed">
                {data.content}
              </div>
            </div>

            <div className="mt-6 pt-6 border-t-2 border-foreground/10">
              <div className="flex items-center justify-between text-xs text-muted-foreground">
                <div className="flex items-center gap-4">
                  <span className="flex items-center gap-1">
                    <Brain className="h-4 w-4" />
                    Generated by GLM-4.7
                  </span>
                  <span className="flex items-center gap-1">
                    <Clock className="h-4 w-4" />
                    {new Date(data.last_updated).toLocaleString()}
                  </span>
                </div>
                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button variant="ghost" size="icon" className="h-8 w-8">
                        <Info className="h-4 w-4" />
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent>
                      <p className="max-w-xs text-xs">
                        This analysis is generated by AI and should not be taken as financial advice.
                        Always consult with a qualified financial advisor.
                      </p>
                    </TooltipContent>
                  </Tooltip>
                </TooltipProvider>
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </Card>
    </div>
  )
}
