'use client'

import { useState, useMemo } from 'react'
import { Template, TemplateType } from '@/lib/types/ai-templates'
import { TemplateCard } from './TemplateCard'
import { TemplateFilter } from './TemplateFilter'
import { cn } from '@/lib/utils'
import { Skeleton } from '@/components/ui/skeleton'
import { Grid3X3, List, FileText } from 'lucide-react'

interface TemplateListProps {
  templates: Template[] | null
  isLoading: boolean
  error?: string | null
  onSelectTemplate: (template: Template) => void
  onRefreshTemplate?: (template: Template) => void
  viewMode?: 'grid' | 'list'
  onViewModeChange?: (mode: 'grid' | 'list') => void
}

export function TemplateList({
  templates,
  isLoading,
  error,
  onSelectTemplate,
  onRefreshTemplate,
  viewMode = 'grid',
  onViewModeChange
}: TemplateListProps) {
  const [filters, setFilters] = useState<{
    search?: string
    templateType?: TemplateType | null
    tier?: 'all' | 'free' | 'premium'
    assetClass?: string | null
  }>({})

  const filteredTemplates = useMemo(() => {
    if (!templates) return []

    return templates.filter((template) => {
      if (filters.search) {
        const searchLower = filters.search.toLowerCase()
        const matchesSearch =
          template.title.toLowerCase().includes(searchLower) ||
          template.summary.toLowerCase().includes(searchLower) ||
          (template.symbol?.toLowerCase().includes(searchLower))
        if (!matchesSearch) return false
      }

      if (filters.templateType && template.template_type !== filters.templateType) {
        return false
      }

      if (filters.tier && filters.tier !== 'all') {
        const tierMap: Record<string, 'free' | 'premium' | 'locked'> = {
          free: 'free',
          premium: 'premium',
        }
        if (template.access_tier !== tierMap[filters.tier]) {
          return false
        }
      }

      if (filters.assetClass && template.asset_class !== filters.assetClass) {
        return false
      }

      return true
    })
  }, [templates, filters])

  if (error) {
    return (
      <div className="flex flex-col items-center justify-center py-12 text-center">
        <FileText className="w-12 h-12 text-muted-foreground mb-4" />
        <h3 className="font-bold text-lg mb-2">Failed to load templates</h3>
        <p className="text-muted-foreground text-sm">{error}</p>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <TemplateFilter onFilterChange={setFilters} />

      {/* Results header */}
      <div className="flex items-center justify-between">
        <p className="text-sm text-muted-foreground">
          {isLoading ? 'Loading...' : `${filteredTemplates.length} template${filteredTemplates.length !== 1 ? 's' : ''}`}
        </p>

        {onViewModeChange && (
          <div className="flex items-center border rounded-none">
            <button
              onClick={() => onViewModeChange('grid')}
              className={cn(
                'p-2 transition-colors',
                viewMode === 'grid' ? 'bg-primary text-primary-foreground' : 'hover:bg-accent'
              )}
            >
              <Grid3X3 className="w-4 h-4" />
            </button>
            <button
              onClick={() => onViewModeChange('list')}
              className={cn(
                'p-2 transition-colors',
                viewMode === 'list' ? 'bg-primary text-primary-foreground' : 'hover:bg-accent'
              )}
            >
              <List className="w-4 h-4" />
            </button>
          </div>
        )}
      </div>

      {/* Templates grid/list */}
      {isLoading ? (
        <div className={cn(
          'grid gap-4',
          viewMode === 'grid' ? 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3' : 'grid-cols-1'
        )}>
          {[...Array(6)].map((_, i) => (
            <div key={i} className="border rounded-none p-4">
              <div className="flex items-center justify-between mb-3">
                <Skeleton className="h-4 w-20" />
                <Skeleton className="h-6 w-16" />
              </div>
              <Skeleton className="h-6 w-full mb-2" />
              <Skeleton className="h-4 w-3/4 mb-4" />
              <div className="flex items-center gap-2">
                <Skeleton className="h-3 w-16" />
              </div>
            </div>
          ))}
        </div>
      ) : filteredTemplates.length > 0 ? (
        <div className={cn(
          'grid gap-4',
          viewMode === 'grid' ? 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3' : 'grid-cols-1'
        )}>
          {filteredTemplates.map((template) => (
            <TemplateCard
              key={template.id}
              template={template}
              onSelect={onSelectTemplate}
              onRefresh={onRefreshTemplate}
              showDelta={template.is_stale}
              delta={
                template.is_stale
                  ? {
                      direction: 'neutral' as const,
                      change: 0,
                      message: 'New data available - click to update'
                    }
                  : null
              }
            />
          ))}
        </div>
      ) : (
        <div className="flex flex-col items-center justify-center py-16 text-center">
          <FileText className="w-12 h-12 text-muted-foreground mb-4" />
          <h3 className="font-bold text-lg mb-2">No templates found</h3>
          <p className="text-muted-foreground text-sm">
            {Object.keys(filters).length > 0
              ? 'Try adjusting your filters to see more results.'
              : 'No templates available at the moment.'}
          </p>
        </div>
      )}
    </div>
  )
}
