'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import { SuggestedStrategy } from '@/lib/types/ai-advisor'
import { TrendingUp, TrendingDown, Zap, Target, DollarSign, AlertTriangle } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { getRiskBgColor, getRiskColor } from '@/lib/types/ai-advisor'

interface StrategyCardProps {
  strategy: SuggestedStrategy
  onSelect?: (strategy: SuggestedStrategy) => void
  className?: string
}

export function StrategyCard({ strategy, onSelect, className }: StrategyCardProps) {
  return (
    <div className={cn(
      'brutalist-glass border-2 border-foreground bg-background group hover:translate-y-[-2px] transition-all',
      className
    )}>
      <div className="p-4 border-b-2 border-foreground flex items-start justify-between">
        <div className="flex items-center gap-3">
          <div className={cn(
            'h-10 w-10 flex items-center justify-center border-2',
            getRiskBgColor(strategy.risk_level).replace('bg-', 'bg-').replace('/10', '/20'),
            getRiskColor(strategy.risk_level).replace('text-', 'border-')
          )}>
            <Target className={cn('h-5 w-5', getRiskColor(strategy.risk_level))} />
          </div>
          <div>
            <h3 className="font-black uppercase text-sm">{strategy.name}</h3>
            <p className="text-xs text-muted-foreground">{strategy.type}</p>
          </div>
        </div>
        <Badge className={cn('rounded-none font-mono text-[9px]', getRiskBgColor(strategy.risk_level))}>
          {strategy.risk_level.toUpperCase()} RISK
        </Badge>
      </div>

      <div className="p-4 space-y-3">
        <p className="text-xs leading-relaxed">{strategy.rationale}</p>

        <div className="grid grid-cols-2 gap-2">
          <div className="border-2 border-foreground/10 p-2">
            <div className="flex items-center gap-1 text-[9px] font-mono uppercase text-muted-foreground mb-1">
              <DollarSign className="h-3 w-3" /> Capital Required
            </div>
            <div className="font-black font-mono text-sm">
              ${strategy.capital_required.toLocaleString()}
            </div>
          </div>
          <div className="border-2 border-foreground/10 p-2">
            <div className="flex items-center gap-1 text-[9px] font-mono uppercase text-muted-foreground mb-1">
              <TrendingUp className="h-3 w-3" /> Expected Return
            </div>
            <div className={cn(
              'font-black font-mono text-sm',
              strategy.expected_return.includes('+') ? 'text-green-500' : ''
            )}>
              {strategy.expected_return}
            </div>
          </div>
        </div>

        <div className="border-2 border-foreground/10 p-2 space-y-1">
          <div className="flex justify-between text-[9px] font-mono uppercase text-muted-foreground">
            <span>Max Profit</span>
            <span>Max Loss</span>
            <span>Breakeven</span>
          </div>
          <div className="flex justify-between font-black font-mono text-sm">
            <span className="text-green-500">{strategy.key_metrics.max_profit}</span>
            <span className="text-red-500">{strategy.key_metrics.max_loss}</span>
            <span>{strategy.key_metrics.breakeven}</span>
          </div>
        </div>

        {strategy.setup_steps && strategy.setup_steps.length > 0 && (
          <details className="group/details">
            <summary className="text-[10px] font-black uppercase cursor-pointer flex items-center gap-2">
              <Zap className="h-3 w-3" /> Setup Steps
              <span className="ml-auto opacity-50 group-open/details:rotate-90 transition-transform"></span>
            </summary>
            <ol className="mt-2 space-y-1 text-[9px] font-mono">
              {strategy.setup_steps.slice(0, 3).map((step, i) => (
                <li key={i} className="flex gap-2">
                  <span className="text-muted-foreground">{i + 1}.</span>
                  <span>{step}</span>
                </li>
              ))}
              {strategy.setup_steps.length > 3 && (
                <li className="text-muted-foreground italic">
                  +{strategy.setup_steps.length - 3} more steps...
                </li>
              )}
            </ol>
          </details>
        )}
      </div>

      {onSelect && (
        <div className="p-4 border-t-2 border-foreground">
          <Button
            onClick={() => onSelect(strategy)}
            className="w-full h-10 rounded-none border-2 border-foreground bg-foreground text-background hover:bg-primary hover:text-primary-foreground font-black uppercase text-xs transition-all"
          >
            Apply Strategy
          </Button>
        </div>
      )}
    </div>
  )
}

interface StrategyGridProps {
  strategies: SuggestedStrategy[]
  onSelect?: (strategy: SuggestedStrategy) => void
  className?: string
}

export function StrategyGrid({ strategies, onSelect, className }: StrategyGridProps) {
  if (!strategies || strategies.length === 0) {
    return (
      <div className={cn('brutalist-glass border-2 border-foreground/20 p-8 text-center', className)}>
        <AlertTriangle className="h-8 w-8 mx-auto text-muted-foreground mb-3" />
        <p className="font-mono text-xs uppercase text-muted-foreground">
          No strategies available for the current criteria
        </p>
      </div>
    )
  }

  return (
    <div className={cn('grid gap-4', className)}>
      {strategies.map((strategy, i) => (
        <StrategyCard key={i} strategy={strategy} onSelect={onSelect} />
      ))}
    </div>
  )
}
