'use client'

import { useState, useCallback } from 'react'
import { TemplateType, TEMPLATE_TYPE_LABELS, TEMPLATE_TIERS } from '@/lib/types/ai-templates'
import { cn } from '@/lib/utils'
import {
  Search,
  Filter,
  X,
  ChevronDown,
  BarChart3,
  TrendingUp,
  Shield,
  Newspaper,
  Activity,
  Briefcase,
  Coins,
  Calendar,
  Globe
} from 'lucide-react'

interface TemplateFilterProps {
  onFilterChange: (filters: {
    search?: string
    templateType?: TemplateType | null
    tier?: 'all' | 'free' | 'premium'
    assetClass?: string | null
  }) => void
  className?: string
}

const TEMPLATE_TYPE_ICONS: Record<TemplateType, React.ReactNode> = {
  market_summary: <BarChart3 className="w-4 h-4" />,
  asset_analysis: <TrendingUp className="w-4 h-4" />,
  sector_report: <BarChart3 className="w-4 h-4" />,
  risk_commentary: <Shield className="w-4 h-4" />,
  sentiment_summary: <Newspaper className="w-4 h-4" />,
  volatility_outlook: <Activity className="w-4 h-4" />,
  options_strategy: <TrendingUp className="w-4 h-4" />,
  bond_market: <Briefcase className="w-4 h-4" />,
  crypto_market: <Coins className="w-4 h-4" />,
  earnings_preview: <Calendar className="w-4 h-4" />,
  macro_outlook: <Globe className="w-4 h-4" />,
}

const ASSET_CLASSES = [
  { value: 'equity', label: 'Equity', icon: <TrendingUp className="w-4 h-4" /> },
  { value: 'fixed_income', label: 'Fixed Income', icon: <Briefcase className="w-4 h-4" /> },
  { value: 'crypto', label: 'Crypto', icon: <Coins className="w-4 h-4" /> },
  { value: 'commodity', label: 'Commodity', icon: <BarChart3 className="w-4 h-4" /> },
  { value: 'forex', label: 'Forex', icon: <Globe className="w-4 h-4" /> },
]

export function TemplateFilter({ onFilterChange, className }: TemplateFilterProps) {
  const [search, setSearch] = useState('')
  const [templateType, setTemplateType] = useState<TemplateType | null>(null)
  const [tier, setTier] = useState<'all' | 'free' | 'premium'>('all')
  const [assetClass, setAssetClass] = useState<string | null>(null)
  const [showTypeDropdown, setShowTypeDropdown] = useState(false)
  const [showAssetDropdown, setShowAssetDropdown] = useState(false)

  const handleFilterChange = useCallback(
    (key: string, value: string | null) => {
      const newFilters: Parameters<typeof onFilterChange>[0] = {
        search: search || undefined,
        templateType: templateType || undefined,
        tier: tier === 'all' ? undefined : tier,
        assetClass: assetClass || undefined,
      }
      if (key === 'search') newFilters.search = value || undefined
      if (key === 'templateType') newFilters.templateType = value as TemplateType | undefined || null
      if (key === 'tier') newFilters.tier = (value as 'all' | 'free' | 'premium') || 'all'
      if (key === 'assetClass') newFilters.assetClass = value || undefined
      onFilterChange(newFilters)
    },
    [search, templateType, tier, assetClass, onFilterChange]
  )

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value)
    handleFilterChange('search', e.target.value)
  }

  const clearFilters = () => {
    setSearch('')
    setTemplateType(null)
    setTier('all')
    setAssetClass(null)
    onFilterChange({})
  }

  const hasActiveFilters = search || templateType || tier !== 'all' || assetClass

  return (
    <div className={cn('space-y-4', className)}>
      {/* Search bar */}
      <div className="relative">
        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
        <input
          type="text"
          value={search}
          onChange={handleSearchChange}
          placeholder="Search templates..."
          className={cn(
            'w-full h-10 pl-10 pr-4 rounded-none border bg-background',
            'placeholder:text-muted-foreground',
            'focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary'
          )}
        />
        {search && (
          <button
            onClick={() => {
              setSearch('')
              handleFilterChange('search', '')
            }}
            className="absolute right-3 top-1/2 -translate-y-1/2 text-muted-foreground hover:text-foreground"
          >
            <X className="w-4 h-4" />
          </button>
        )}
      </div>

      {/* Filter row */}
      <div className="flex flex-wrap items-center gap-3">
        {/* Template type dropdown */}
        <div className="relative">
          <button
            onClick={() => setShowTypeDropdown(!showTypeDropdown)}
            className={cn(
              'flex items-center gap-2 h-10 px-3 rounded-none border bg-background',
              'hover:border-primary/50 transition-colors',
              templateType && 'border-primary'
            )}
          >
            <Filter className="w-4 h-4 text-muted-foreground" />
            <span className="text-sm">
              {templateType ? TEMPLATE_TYPE_LABELS[templateType] : 'All Types'}
            </span>
            <ChevronDown className={cn(
              'w-4 h-4 text-muted-foreground transition-transform',
              showTypeDropdown && 'rotate-180'
            )} />
          </button>

          {showTypeDropdown && (
            <>
              <div className="fixed inset-0 z-10" onClick={() => setShowTypeDropdown(false)} />
              <div className="absolute top-full left-0 mt-1 z-20 w-64 max-h-80 overflow-y-auto bg-popover border rounded-none shadow-lg">
                <button
                  onClick={() => {
                    setTemplateType(null)
                    handleFilterChange('templateType', null)
                    setShowTypeDropdown(false)
                  }}
                  className={cn(
                    'w-full px-3 py-2 text-left text-sm hover:bg-accent',
                    !templateType && 'bg-accent'
                  )}
                >
                  All Types
                </button>
                {(Object.keys(TEMPLATE_TYPE_LABELS) as TemplateType[]).map((type) => (
                  <button
                    key={type}
                    onClick={() => {
                      setTemplateType(type)
                      handleFilterChange('templateType', type)
                      setShowTypeDropdown(false)
                    }}
                    className={cn(
                      'w-full px-3 py-2 text-left text-sm hover:bg-accent',
                      'flex items-center gap-2',
                      templateType === type && 'bg-accent'
                    )}
                  >
                    {TEMPLATE_TYPE_ICONS[type]}
                    <span>{TEMPLATE_TYPE_LABELS[type]}</span>
                    {!TEMPLATE_TIERS[type].free && (
                      <span className="ml-auto text-xs text-amber-500">Premium</span>
                    )}
                  </button>
                ))}
              </div>
            </>
          )}
        </div>

        {/* Asset class dropdown */}
        <div className="relative">
          <button
            onClick={() => setShowAssetDropdown(!showAssetDropdown)}
            className={cn(
              'flex items-center gap-2 h-10 px-3 rounded-none border bg-background',
              'hover:border-primary/50 transition-colors',
              assetClass && 'border-primary'
            )}
          >
            <span className="text-sm">
              {assetClass
                ? ASSET_CLASSES.find((a) => a.value === assetClass)?.label
                : 'All Assets'}
            </span>
            <ChevronDown className={cn(
              'w-4 h-4 text-muted-foreground transition-transform',
              showAssetDropdown && 'rotate-180'
            )} />
          </button>

          {showAssetDropdown && (
            <>
              <div className="fixed inset-0 z-10" onClick={() => setShowAssetDropdown(false)} />
              <div className="absolute top-full left-0 mt-1 z-20 w-48 bg-popover border rounded-none shadow-lg">
                <button
                  onClick={() => {
                    setAssetClass(null)
                    handleFilterChange('assetClass', null)
                    setShowAssetDropdown(false)
                  }}
                  className={cn(
                    'w-full px-3 py-2 text-left text-sm hover:bg-accent',
                    !assetClass && 'bg-accent'
                  )}
                >
                  All Assets
                </button>
                {ASSET_CLASSES.map((asset) => (
                  <button
                    key={asset.value}
                    onClick={() => {
                      setAssetClass(asset.value)
                      handleFilterChange('assetClass', asset.value)
                      setShowAssetDropdown(false)
                    }}
                    className={cn(
                      'w-full px-3 py-2 text-left text-sm hover:bg-accent',
                      'flex items-center gap-2',
                      assetClass === asset.value && 'bg-accent'
                    )}
                  >
                    {asset.icon}
                    <span>{asset.label}</span>
                  </button>
                ))}
              </div>
            </>
          )}
        </div>

        {/* Tier filter */}
        <div className="flex items-center border rounded-none">
          {(['all', 'free', 'premium'] as const).map((t) => (
            <button
              key={t}
              onClick={() => {
                setTier(t)
                handleFilterChange('tier', t)
              }}
              className={cn(
                'px-3 py-2 text-sm capitalize transition-colors',
                tier === t
                  ? 'bg-primary text-primary-foreground'
                  : 'hover:bg-accent'
              )}
            >
              {t}
            </button>
          ))}
        </div>

        {/* Clear filters */}
        {hasActiveFilters && (
          <button
            onClick={clearFilters}
            className="flex items-center gap-1.5 px-3 py-2 text-sm text-muted-foreground hover:text-foreground transition-colors"
          >
            <X className="w-4 h-4" />
            Clear filters
          </button>
        )}
      </div>

      {/* Active filter tags */}
      {hasActiveFilters && (
        <div className="flex flex-wrap gap-2">
          {templateType && (
            <span className="inline-flex items-center gap-1 px-2 py-1 bg-primary/10 text-primary text-xs rounded-none">
              {TEMPLATE_TYPE_LABELS[templateType]}
              <button onClick={() => {
                setTemplateType(null)
                handleFilterChange('templateType', null)
              }}>
                <X className="w-3 h-3" />
              </button>
            </span>
          )}
          {assetClass && (
            <span className="inline-flex items-center gap-1 px-2 py-1 bg-primary/10 text-primary text-xs rounded-none">
              {ASSET_CLASSES.find((a) => a.value === assetClass)?.label}
              <button onClick={() => {
                setAssetClass(null)
                handleFilterChange('assetClass', null)
              }}>
                <X className="w-3 h-3" />
              </button>
            </span>
          )}
          {tier !== 'all' && (
            <span className="inline-flex items-center gap-1 px-2 py-1 bg-primary/10 text-primary text-xs rounded-none capitalize">
              {tier}
              <button onClick={() => {
                setTier('all')
                handleFilterChange('tier', 'all')
              }}>
                <X className="w-3 h-3" />
              </button>
            </span>
          )}
        </div>
      )}
    </div>
  )
}
