'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip,
  ResponsiveContainer, Area, ComposedChart
} from 'recharts'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Skeleton } from '@/components/ui/skeleton'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Tooltip as TooltipUI,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'
import {
  Activity, RefreshCw, Clock, Info, Brain, BarChart3, Zap, Target
} from 'lucide-react'
import { VolatilityOutlookResponse } from '@/lib/types/ai-advisor'

interface VolatilityOutlookProps {
  data?: VolatilityOutlookResponse & {
    vix?: number
    vixPercentile?: number
    historicalVol?: { date: string; vol: number }[]
    projectedRange?: { date: string; upper: number; lower: number }[]
  }
  symbol?: string
  loading?: boolean
  onRefresh?: () => void
  className?: string
}

export function VolatilityOutlook({
  data,
  symbol = 'SPY',
  loading = false,
  onRefresh,
  className
}: VolatilityOutlookProps) {
  const [activeTab, setActiveTab] = React.useState('overview')

  const chartData = React.useMemo(() => {
    if (!data?.historicalVol) return []
    return data.historicalVol.map(d => ({
      date: new Date(d.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
      volatility: d.vol * 100
    }))
  }, [data?.historicalVol])

  const projectedData = React.useMemo(() => {
    if (!data?.projectedRange) return []
    return data.projectedRange.map((d, i) => ({
      date: `Day ${i + 1}`,
      upper: d.upper * 100,
      lower: d.lower * 100,
      mid: ((d.upper + d.lower) / 2) * 100
    }))
  }, [data?.projectedRange])

  const volLevel = data?.vix ? (
    data.vix < 15 ? 'Low' :
    data.vix < 20 ? 'Moderate' :
    data.vix < 30 ? 'Elevated' : 'High'
  ) : 'Unknown'

  const volColor = volLevel === 'Low' ? 'text-green-500' :
    volLevel === 'Moderate' ? 'text-blue-500' :
    volLevel === 'Elevated' ? 'text-yellow-500' : 'text-red-500'

  const bgColor = volLevel === 'Low' ? 'bg-green-500/10' :
    volLevel === 'Moderate' ? 'bg-blue-500/10' :
    volLevel === 'Elevated' ? 'bg-yellow-500/10' : 'bg-red-500/10'

  if (loading) {
    return (
      <div className={cn('space-y-4', className)}>
        <div className="flex items-center justify-between">
          <Skeleton className="h-12 w-64" />
          <Skeleton className="h-10 w-24" />
        </div>
        <Skeleton className="h-96 border-2 border-foreground" />
      </div>
    )
  }

  if (!data) {
    return (
      <Card className={cn('rounded-none border-2 border-foreground', className)}>
        <CardContent className="p-12 text-center">
          <Activity className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
          <h3 className="font-black uppercase text-lg mb-2">No Volatility Data</h3>
          <p className="text-muted-foreground font-mono text-xs mb-4">
            AI-powered volatility outlook for {symbol} will appear here
          </p>
          {onRefresh && (
            <Button
              onClick={onRefresh}
              variant="outline"
              className="rounded-none border-2 font-bold uppercase"
            >
              <RefreshCw className="h-4 w-4 mr-2" />
              Load Analysis
            </Button>
          )}
        </CardContent>
      </Card>
    )
  }

  return (
    <div className={cn('space-y-4', className)}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className={cn('h-12 w-12 flex items-center justify-center border-2 border-foreground shadow-[4px_4px_0px_0px_var(--foreground)]', bgColor)}>
            <Activity className={cn('h-6 w-6', volColor)} />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-lg">{data.title}</span>
              <Badge className={cn('rounded-none font-mono text-xs', bgColor, volColor)}>
                {volLevel} Volatility
              </Badge>
            </div>
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <span className="font-mono text-xs">{symbol} - VIX: {data.vix?.toFixed(2) || 'N/A'}</span>
              <span className="text-xs">-</span>
              <Clock className="h-3 w-3" />
              <span className="font-mono text-xs">{new Date(data.last_updated).toLocaleString()}</span>
            </div>
          </div>
        </div>
        {onRefresh && (
          <Button
            onClick={onRefresh}
            variant="outline"
            size="sm"
            className="rounded-none border-2 font-bold uppercase"
          >
            <RefreshCw className="h-4 w-4 mr-2" />
            Refresh
          </Button>
        )}
      </div>

      <Card className="rounded-none border-2 border-foreground">
        <Tabs value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="h-12 rounded-none border-b-2 bg-muted/50 p-1">
            <TabsTrigger
              value="overview"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Target className="h-4 w-4 mr-2" />
              Overview
            </TabsTrigger>
            <TabsTrigger
              value="history"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <BarChart3 className="h-4 w-4 mr-2" />
              History
            </TabsTrigger>
            <TabsTrigger
              value="projection"
              className="rounded-none data-[state=active]:bg-foreground data-[state=active]:text-background font-black uppercase text-xs px-4"
            >
              <Zap className="h-4 w-4 mr-2" />
              Projection
            </TabsTrigger>
          </TabsList>

          <TabsContent value="overview" className="m-0 p-6">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">VIX Level</div>
                <div className={cn('text-2xl font-black font-mono', volColor)}>
                  {data.vix?.toFixed(2) || 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">VIX Percentile</div>
                <div className="text-2xl font-black font-mono">
                  {data.vixPercentile !== undefined ? `${data.vixPercentile.toFixed(0)}%` : 'N/A'}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Outlook</div>
                <div className={cn('text-lg font-black', volColor)}>
                  {volLevel}
                </div>
              </div>
              <div className="p-4 bg-muted/20 border-2 border-foreground/10">
                <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Signal</div>
                <div className="text-lg font-black">
                  {volLevel === 'High' ? 'Risk Off' : volLevel === 'Low' ? 'Risk On' : 'Neutral'}
                </div>
              </div>
            </div>

            <div className="p-4 bg-muted/20 border-2 border-foreground/10">
              <p className="text-sm leading-relaxed">{data.content}</p>
            </div>
          </TabsContent>

          <TabsContent value="history" className="m-0 p-6">
            {chartData.length > 0 ? (
              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
                    <XAxis dataKey="date" tick={{ fontSize: 11, fontFamily: 'monospace' }} />
                    <YAxis tick={{ fontSize: 11, fontFamily: 'monospace' }} tickFormatter={(v) => `${v.toFixed(0)}%`} />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'var(--background)',
                        border: '2px solid var(--foreground)',
                        borderRadius: 0,
                        boxShadow: '4px 4px 0px 0px var(--foreground)',
                      }}
                      formatter={(value: number) => [`${value.toFixed(2)}%`, 'Volatility']}
                    />
                    <Line
                      type="monotone"
                      dataKey="volatility"
                      stroke="var(--primary)"
                      strokeWidth={2}
                      dot={{ r: 3, fill: 'var(--primary)' }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            ) : (
              <div className="p-12 text-center text-muted-foreground">
                <BarChart3 className="h-8 w-8 mx-auto mb-3 opacity-50" />
                <p className="text-sm font-mono uppercase">No historical data available</p>
              </div>
            )}
          </TabsContent>

          <TabsContent value="projection" className="m-0 p-6">
            {projectedData.length > 0 ? (
              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <ComposedChart data={projectedData}>
                    <defs>
                      <linearGradient id="volGradient" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="var(--primary)" stopOpacity={0.3} />
                        <stop offset="95%" stopColor="var(--primary)" stopOpacity={0} />
                      </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
                    <XAxis dataKey="date" tick={{ fontSize: 11, fontFamily: 'monospace' }} />
                    <YAxis tick={{ fontSize: 11, fontFamily: 'monospace' }} tickFormatter={(v) => `${v.toFixed(0)}%`} />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'var(--background)',
                        border: '2px solid var(--foreground)',
                        borderRadius: 0,
                        boxShadow: '4px 4px 0px 0px var(--foreground)',
                      }}
                      formatter={(value: number, name: string) => [
                        `${value.toFixed(2)}%`,
                        name === 'upper' ? 'Upper' : name === 'lower' ? 'Lower' : 'Mid'
                      ]}
                    />
                    <Area
                      type="monotone"
                      dataKey="upper"
                      stroke="none"
                      fill="url(#volGradient)"
                    />
                    <Area
                      type="monotone"
                      dataKey="lower"
                      stroke="none"
                      fill="var(--background)"
                    />
                    <Line type="monotone" dataKey="mid" stroke="var(--primary)" strokeWidth={2} dot={{ r: 4, fill: 'var(--primary)' }} />
                  </ComposedChart>
                </ResponsiveContainer>
              </div>
            ) : (
              <div className="p-12 text-center text-muted-foreground">
                <Zap className="h-8 w-8 mx-auto mb-3 opacity-50" />
                <p className="text-sm font-mono uppercase">No projections available</p>
              </div>
            )}
          </TabsContent>
        </Tabs>
      </Card>
    </div>
  )
}
