'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip,
  ResponsiveContainer, Area, ComposedChart, ReferenceLine
} from 'recharts'
import { TrendingUp, TrendingDown, Brain, Target, Clock, CheckCircle, AlertCircle } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Tooltip as TooltipUI,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from '@/components/ui/tooltip'

interface PricePredictionProps {
  symbol: string
  currentPrice: number
  predictedPrice: number
  confidenceLower: number
  confidenceUpper: number
  horizon: string
  modelName: string
  modelAccuracy: number
  lastUpdated: string
  keyFactors: string[]
  sentiment: 'bullish' | 'bearish' | 'neutral'
  sentimentScore: number
  narrative: string
  historicalPredictions?: HistoricalPrediction[]
  className?: string
}

interface HistoricalPrediction {
  date: string
  predictedPrice: number
  actualPrice: number
  horizon: string
}

export function PricePrediction({
  symbol,
  currentPrice,
  predictedPrice,
  confidenceLower,
  confidenceUpper,
  horizon,
  modelName,
  modelAccuracy,
  lastUpdated,
  keyFactors,
  sentiment,
  sentimentScore,
  narrative,
  historicalPredictions = [],
  className
}: PricePredictionProps) {
  const change = ((predictedPrice - currentPrice) / currentPrice) * 100
  const isPositive = change >= 0
  const midConfidence = (confidenceLower + confidenceUpper) / 2

  const chartData = React.useMemo(() => {
    const data = [
      {
        date: 'Now',
        type: 'current',
        price: currentPrice,
        isReference: true
      },
      {
        date: `Predicted (${horizon})`,
        type: 'predicted',
        price: predictedPrice,
        upper: confidenceUpper,
        lower: confidenceLower
      }
    ]
    return data
  }, [currentPrice, predictedPrice, confidenceUpper, confidenceLower, horizon])

  const accuracyChartData = React.useMemo(() => {
    return historicalPredictions.map(hp => ({
      date: new Date(hp.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
      predicted: hp.predictedPrice,
      actual: hp.actualPrice,
      error: Math.abs(hp.predictedPrice - hp.actualPrice) / hp.actualPrice * 100
    }))
  }, [historicalPredictions])

  return (
    <div className={cn('space-y-4', className)}>
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="h-12 w-12 bg-primary/10 flex items-center justify-center border-2 border-primary/30">
            <Brain className="h-6 w-6 text-primary" />
          </div>
          <div>
            <div className="flex items-center gap-2">
              <span className="font-black uppercase text-lg">{symbol}</span>
              <Badge className={cn(
                'rounded-none font-mono text-xs',
                sentiment === 'bullish' ? 'bg-green-500' :
                sentiment === 'bearish' ? 'bg-red-500' : 'bg-muted'
              )}>
                {sentiment.toUpperCase()}
              </Badge>
            </div>
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <span>AI Price Prediction</span>
              <span className="text-xs"></span>
              <span className="flex items-center gap-1">
                <Clock className="h-3 w-3" />
                Updated {lastUpdated}
              </span>
            </div>
          </div>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm" className="h-8 rounded-none border-2">
            <Target className="h-4 w-4 mr-2" />
            Run Prediction
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card className="rounded-none border-2">
          <CardContent className="p-4">
            <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Current Price</div>
            <div className="text-2xl font-black font-mono">${currentPrice.toLocaleString()}</div>
          </CardContent>
        </Card>
        <Card className={cn('rounded-none border-2', isPositive ? 'border-green-500/50' : 'border-red-500/50')}>
          <CardContent className="p-4">
            <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Predicted ({horizon})</div>
            <div className={cn('text-2xl font-black font-mono', isPositive ? 'text-green-500' : 'text-red-500')}>
              ${predictedPrice.toFixed(2)}
            </div>
            <div className={cn('text-xs font-bold flex items-center gap-1', isPositive ? 'text-green-500' : 'text-red-500')}>
              {isPositive ? <TrendingUp className="h-3 w-3" /> : <TrendingDown className="h-3 w-3" />}
              {change >= 0 ? '+' : ''}{change.toFixed(2)}%
            </div>
          </CardContent>
        </Card>
        <Card className="rounded-none border-2">
          <CardContent className="p-4">
            <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Confidence Range</div>
            <div className="text-lg font-black font-mono">${confidenceLower.toFixed(2)} - ${confidenceUpper.toFixed(2)}</div>
            <Progress value={75} className="h-1 mt-2" />
          </CardContent>
        </Card>
        <Card className="rounded-none border-2">
          <CardContent className="p-4">
            <div className="text-xs font-mono uppercase text-muted-foreground mb-1">Model Accuracy</div>
            <div className="flex items-center gap-2">
              <div className="text-2xl font-black font-mono">{modelAccuracy}%</div>
              <TooltipProvider>
                <TooltipUI>
                  <TooltipTrigger asChild>
                    <Button variant="ghost" size="icon" className="h-6 w-6">
                      <AlertCircle className="h-4 w-4 text-muted-foreground" />
                    </Button>
                  </TooltipTrigger>
                  <TooltipContent>
                    <p>{modelName} model accuracy over 90 days</p>
                  </TooltipContent>
                </TooltipUI>
              </TooltipProvider>
            </div>
            <div className="flex items-center gap-1 mt-1">
              {modelAccuracy >= 70 ? (
                <CheckCircle className="h-3 w-3 text-green-500" />
              ) : (
                <AlertCircle className="h-3 w-3 text-yellow-500" />
              )}
              <span className="text-xs text-muted-foreground">{modelName}</span>
            </div>
          </CardContent>
        </Card>
      </div>

      <Tabs defaultValue="forecast" className="w-full">
        <TabsList className="grid w-full grid-cols-3 rounded-none border-2 bg-muted/50">
          <TabsTrigger value="forecast" className="rounded-none">Forecast</TabsTrigger>
          <TabsTrigger value="factors" className="rounded-none">Key Factors</TabsTrigger>
          <TabsTrigger value="accuracy" className="rounded-none">Model Accuracy</TabsTrigger>
        </TabsList>

        <TabsContent value="forecast" className="mt-4">
          <Card className="rounded-none border-2">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-black uppercase">Price Forecast</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="h-64">
                <ResponsiveContainer width="100%" height="100%">
                  <ComposedChart data={chartData} margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
                    <defs>
                      <linearGradient id="confidenceGradientPred" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="var(--primary)" stopOpacity={0.3} />
                        <stop offset="95%" stopColor="var(--primary)" stopOpacity={0} />
                      </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
                    <XAxis dataKey="date" tick={{ fontSize: 11, fontFamily: 'monospace' }} />
                    <YAxis
                      domain={[Math.min(currentPrice, confidenceLower) * 0.95, Math.max(currentPrice, confidenceUpper) * 1.05]}
                      tick={{ fontSize: 11, fontFamily: 'monospace' }}
                      tickFormatter={(value) => `$${(value / 1000).toFixed(1)}k`}
                    />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'var(--background)',
                        border: '2px solid var(--foreground)',
                        borderRadius: 0,
                        boxShadow: '4px 4px 0px 0px var(--foreground)',
                      }}
                      formatter={(value: number, name: string) => [
                        `$${value.toFixed(2)}`,
                        name === 'price' ? 'Current' : name === 'predicted' ? 'Predicted' : name
                      ]}
                    />
                    <ReferenceLine y={currentPrice} stroke="var(--muted-foreground)" strokeDasharray="5 5" />
                    <Area
                      type="monotone"
                      dataKey="upper"
                      stroke="none"
                      fill="url(#confidenceGradientPred)"
                      fillOpacity={1}
                    />
                    <Area
                      type="monotone"
                      dataKey="lower"
                      stroke="none"
                      fill="var(--background)"
                      fillOpacity={1}
                    />
                    <Line
                      type="monotone"
                      dataKey="price"
                      stroke="var(--muted-foreground)"
                      strokeWidth={3}
                      dot={{ r: 6, fill: 'var(--muted-foreground)', strokeWidth: 2, stroke: 'var(--background)' }}
                    />
                    <Line
                      type="monotone"
                      dataKey="predicted"
                      stroke="var(--primary)"
                      strokeWidth={3}
                      dot={{ r: 6, fill: 'var(--primary)', strokeWidth: 2, stroke: 'var(--background)' }}
                    />
                  </ComposedChart>
                </ResponsiveContainer>
              </div>
              <div className="mt-4 p-3 bg-muted/20 border-2 border-foreground/10">
                <p className="text-xs leading-relaxed">{narrative}</p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="factors" className="mt-4">
          <Card className="rounded-none border-2">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-black uppercase">Key Prediction Factors</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-3">
                  <h4 className="text-xs font-mono uppercase text-muted-foreground">Positive Factors</h4>
                  {keyFactors.filter((_, i) => i % 2 === 0).map((factor, i) => (
                    <div key={i} className="flex items-start gap-2 p-2 bg-green-500/5 border border-green-500/20">
                      <TrendingUp className="h-4 w-4 text-green-500 mt-0.5" />
                      <span className="text-sm">{factor}</span>
                    </div>
                  ))}
                </div>
                <div className="space-y-3">
                  <h4 className="text-xs font-mono uppercase text-muted-foreground">Risk Factors</h4>
                  {keyFactors.filter((_, i) => i % 2 === 1).map((factor, i) => (
                    <div key={i} className="flex items-start gap-2 p-2 bg-red-500/5 border border-red-500/20">
                      <TrendingDown className="h-4 w-4 text-red-500 mt-0.5" />
                      <span className="text-sm">{factor}</span>
                    </div>
                  ))}
                </div>
              </div>
              <div className="mt-4 pt-4 border-t-2 border-foreground/10">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-xs font-mono uppercase text-muted-foreground">Sentiment Score</span>
                  <span className={cn(
                    'text-sm font-bold font-mono',
                    sentimentScore >= 0.5 ? 'text-green-500' :
                    sentimentScore <= -0.5 ? 'text-red-500' : 'text-yellow-500'
                  )}>
                    {sentimentScore >= 0 ? '+' : ''}{sentimentScore.toFixed(2)}
                  </span>
                </div>
                <Progress
                  value={(sentimentScore + 1) * 50}
                  className={cn(
                    'h-2',
                    sentimentScore >= 0.5 ? '[&>div]:bg-green-500' :
                    sentimentScore <= -0.5 ? '[&>div]:bg-red-500' : '[&>div]:bg-yellow-500'
                  )}
                />
                <div className="flex justify-between mt-1 text-[10px] font-mono text-muted-foreground">
                  <span>Bearish</span>
                  <span>Neutral</span>
                  <span>Bullish</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="accuracy" className="mt-4">
          <Card className="rounded-none border-2">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-black uppercase">Historical Prediction Accuracy</CardTitle>
            </CardHeader>
            <CardContent>
              {historicalPredictions.length > 0 ? (
                <div className="space-y-4">
                  <div className="h-48">
                    <ResponsiveContainer width="100%" height="100%">
                      <LineChart data={accuracyChartData}>
                        <CartesianGrid strokeDasharray="3 3" stroke="var(--border)" opacity={0.3} />
                        <XAxis dataKey="date" tick={{ fontSize: 10, fontFamily: 'monospace' }} />
                        <YAxis tick={{ fontSize: 10, fontFamily: 'monospace' }} tickFormatter={(v) => `$${v}`} />
                        <Tooltip
                          contentStyle={{
                            backgroundColor: 'var(--background)',
                            border: '2px solid var(--foreground)',
                            borderRadius: 0,
                          }}
                          formatter={(value: number, name: string) => [
                            `$${value.toFixed(2)}`,
                            name === 'predicted' ? 'Predicted' : 'Actual'
                          ]}
                        />
                        <Line type="monotone" dataKey="predicted" stroke="var(--primary)" strokeWidth={2} dot={{ r: 3 }} />
                        <Line type="monotone" dataKey="actual" stroke="var(--muted-foreground)" strokeWidth={2} dot={{ r: 3 }} strokeDasharray="5 5" />
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                  <div className="grid grid-cols-3 gap-4">
                    <div className="p-3 bg-muted/20 border-2 border-foreground/10 text-center">
                      <div className="text-xs font-mono uppercase text-muted-foreground">Avg Error</div>
                      <div className="text-lg font-black font-mono">
                        {accuracyChartData.reduce((sum, d) => sum + d.error, 0) / accuracyChartData.length}%
                      </div>
                    </div>
                    <div className="p-3 bg-muted/20 border-2 border-foreground/10 text-center">
                      <div className="text-xs font-mono uppercase text-muted-foreground">Direction Accuracy</div>
                      <div className="text-lg font-black font-mono">{modelAccuracy}%</div>
                    </div>
                    <div className="p-3 bg-muted/20 border-2 border-foreground/10 text-center">
                      <div className="text-xs font-mono uppercase text-muted-foreground">Predictions</div>
                      <div className="text-lg font-black font-mono">{historicalPredictions.length}</div>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="p-8 text-center text-muted-foreground">
                  <Brain className="h-8 w-8 mx-auto mb-3 opacity-50" />
                  <p className="text-sm font-mono uppercase">No historical predictions available</p>
                  <p className="text-xs mt-1">Run predictions to build model history</p>
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}

interface PricePredictionGridProps {
  predictions: PricePredictionProps[]
  className?: string
}

export function PricePredictionGrid({ predictions, className }: PricePredictionGridProps) {
  if (!predictions || predictions.length === 0) {
    return (
      <div className={cn('p-8 text-center border-2 border-dashed border-foreground/20', className)}>
        <Brain className="h-8 w-8 mx-auto text-muted-foreground mb-3" />
        <p className="text-sm font-mono uppercase text-muted-foreground">No predictions available</p>
      </div>
    )
  }

  return (
    <div className={cn('grid gap-4', className)}>
      {predictions.map((prediction, i) => (
        <PricePrediction key={i} {...prediction} />
      ))}
    </div>
  )
}
