// Tier Badge Component - Shows access tier (Free, Premium, Locked)

import { cn } from '@/lib/utils'
import { Lock, Star } from 'lucide-react'

interface TierBadgeProps {
  tier: 'free' | 'premium' | 'locked'
  className?: string
  showLabel?: boolean
  size?: 'sm' | 'md' | 'lg'
}

const tierConfig = {
  free: {
    icon: null,
    label: 'Free',
    bgColor: 'bg-green-500/10',
    borderColor: 'border-green-500/30',
    textColor: 'text-green-500',
    iconColor: null,
  },
  premium: {
    icon: Star,
    label: 'Premium',
    bgColor: 'bg-amber-500/10',
    borderColor: 'border-amber-500/30',
    textColor: 'text-amber-500',
    iconColor: 'text-amber-500',
  },
  locked: {
    icon: Lock,
    label: 'Premium',
    bgColor: 'bg-gray-500/10',
    borderColor: 'border-gray-500/30',
    textColor: 'text-gray-500',
    iconColor: 'text-gray-500',
  },
}

export function TierBadge({ tier, className, showLabel = true, size = 'md' }: TierBadgeProps) {
  const config = tierConfig[tier]
  const Icon = config.icon

  const sizeClasses = {
    sm: 'px-1.5 py-0.5 text-[10px]',
    md: 'px-2 py-1 text-xs',
    lg: 'px-3 py-1.5 text-sm',
  }

  const iconSizes = {
    sm: 'w-3 h-3',
    md: 'w-3.5 h-3.5',
    lg: 'w-4 h-4',
  }

  return (
    <span
      className={cn(
        'inline-flex items-center gap-1.5 font-bold uppercase tracking-wider border',
        config.bgColor,
        config.borderColor,
        config.textColor,
        sizeClasses[size],
        className
      )}
    >
      {Icon && <Icon className={cn(iconSizes[size], config.iconColor)} />}
      {showLabel && config.label}
    </span>
  )
}

// Stale Badge with refresh indicator
export function StaleBadge({ onRefresh }: { onRefresh?: () => void }) {
  return (
    <span
      onClick={onRefresh}
      className={cn(
        'inline-flex items-center gap-1.5 px-2 py-1 text-xs font-bold uppercase tracking-wider',
        'bg-yellow-500/10 border-2 border-yellow-500/30 text-yellow-500',
        'cursor-pointer hover:bg-yellow-500/20 transition-colors'
      )}
    >
      <svg
        className="w-3.5 h-3.5 animate-spin"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
      >
        <circle
          className="opacity-25"
          cx="12"
          cy="12"
          r="10"
          stroke="currentColor"
          strokeWidth="4"
        />
        <path
          className="opacity-75"
          fill="currentColor"
          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
        />
      </svg>
      Update Available
    </span>
  )
}

// New Data Badge - animated
export function NewDataBadge() {
  return (
    <span className="inline-flex items-center gap-1 px-2 py-0.5 bg-foreground text-background text-[10px] font-bold uppercase tracking-wider animate-pulse">
      <span className="w-1.5 h-1.5 bg-background rounded-full animate-ping" />
      New Data
    </span>
  )
}
