'use client'

import * as React from 'react'
import { cn } from '@/lib/utils'
import { Button } from '@/components/ui/button'
import { Textarea } from '@/components/ui/textarea'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Loader2, Send, Bot, User } from 'lucide-react'
import { AIChatMessage } from '@/lib/types/ai-advisor'
import { streamChatMessage } from '@/lib/api/ai-advisor'

interface AIChatProps {
  className?: string
  initialMessage?: string
  placeholder?: string
}

const QUICK_PROMPTS = [
  'Analyze my portfolio for risks',
  'What\'s the market outlook for next week?',
  'Suggest a hedging strategy',
  'Explain VaR in simple terms',
]

export function AIChat({
  className,
  initialMessage,
  placeholder = 'Ask about markets, strategies, or portfolio analysis...',
}: AIChatProps) {
  const [messages, setMessages] = React.useState<AIChatMessage[]>(() => {
    if (initialMessage) {
      return [
        {
          id: 'welcome',
          role: 'assistant',
          content: initialMessage,
          timestamp: new Date().toISOString(),
        },
      ]
    }
    return [
      {
        id: 'welcome',
        role: 'assistant',
        content: "I'm your AI Trading Assistant, powered by GLM-4.7. I can help you with market analysis, strategy suggestions, portfolio insights, and risk explanations. What would you like to know?",
        timestamp: new Date().toISOString(),
      },
    ]
  })
  const [input, setInput] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(false)
  const messagesEndRef = React.useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  React.useEffect(() => {
    scrollToBottom()
  }, [messages])

  const handleSend = async () => {
    if (!input.trim() || isLoading) return

    const userMessage: AIChatMessage = {
      id: Date.now().toString(),
      role: 'user',
      content: input.trim(),
      timestamp: new Date().toISOString(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInput('')
    setIsLoading(true)

    try {
      const assistantMessage: AIChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: '',
        timestamp: new Date().toISOString(),
      }

      setMessages((prev) => [...prev, assistantMessage])

      let fullContent = ''
      for await (const token of streamChatMessage({ message: userMessage.content })) {
        fullContent += token
        setMessages((prev) => {
          const updated = [...prev]
          updated[updated.length - 1] = {
            ...assistantMessage,
            content: fullContent,
          }
          return updated
        })
      }
    } catch (error) {
      const errorMessage: AIChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: `I apologize, but I encountered an error: ${error instanceof Error ? error.message : 'Unknown error'}. Please try again.`,
        timestamp: new Date().toISOString(),
      }
      setMessages((prev) => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  const handleQuickPrompt = (prompt: string) => {
    setInput(prompt)
  }

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSend()
    }
  }

  return (
    <div className={cn('flex flex-col h-full', className)}>
      <ScrollArea className="flex-1 p-4">
        <div className="space-y-4">
          {messages.map((message) => (
            <div
              key={message.id}
              className={cn(
                'flex gap-3',
                message.role === 'user' ? 'flex-row-reverse' : 'flex-row'
              )}
            >
              <div
                className={cn(
                  'w-8 h-8 rounded-none flex items-center justify-center shrink-0',
                  message.role === 'assistant'
                    ? 'bg-primary text-primary-foreground'
                    : 'bg-muted'
                )}
              >
                {message.role === 'assistant' ? (
                  <Bot className="h-4 w-4" />
                ) : (
                  <User className="h-4 w-4" />
                )}
              </div>
              <div
                className={cn(
                  'max-w-[80%] rounded-none border-2 p-3',
                  message.role === 'user'
                    ? 'bg-foreground text-background border-foreground'
                    : 'bg-background border-foreground/20'
                )}
              >
                <p className="text-sm whitespace-pre-wrap">{message.content}</p>
              </div>
            </div>
          ))}

          {isLoading && (
            <div className="flex items-center gap-2 text-muted-foreground text-sm">
              <Loader2 className="h-4 w-4 animate-spin" />
              <span>Analyzing...</span>
            </div>
          )}

          <div ref={messagesEndRef} />
        </div>
      </ScrollArea>

      <div className="p-4 border-t-2 border-foreground">
        <div className="flex gap-2 mb-2">
          {QUICK_PROMPTS.map((prompt, idx) => (
            <Button
              key={prompt}
              variant="outline"
              size="sm"
              className="rounded-none border-dashed text-xs"
              onClick={() => handleQuickPrompt(prompt)}
            >
              {prompt}
            </Button>
          ))}
        </div>
        <div className="flex gap-2">
          <Textarea
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder={placeholder}
            className="min-h-[44px] max-h-32 rounded-none border-2 border-foreground resize-none"
            disabled={isLoading}
          />
          <Button
            onClick={handleSend}
            disabled={isLoading || !input.trim()}
            className="h-auto rounded-none border-2 border-foreground bg-foreground text-background hover:bg-primary hover:text-primary-foreground"
          >
            <Send className="h-4 w-4" />
          </Button>
        </div>
      </div>
    </div>
  )
}
